import DefaultLayout from "./DefaultLayout";

function Races(props) {

    const races = [
        {
            date: "October 16, 2020",
            name: "Blazer Classic",
            link: "https://www.tfrrs.org/results/xc/16999/Blazer_Classic"
        },
        {
            date: "November 6, 2020",
            name: "Vulcan Run 10K",
            link: "https://runsignup.com/Race/Results/10038/"
        }
    ];

    const toRow = function(race) {
        var cells = [];
        cells.push( <td>{race.date}</td> );

        if (race.link)
            cells.push( <td><a href={race.link}>{race.name}</a></td>)
        else
            cells.push( <td>{race.name}</td>);

        return (<tr>{cells}</tr>);
    };

    const rows = races.map(toRow);

    return (
        <DefaultLayout>
            <table>
                <tr>
                    <th>Date</th>
                    <th>Race</th>
                </tr>
                {rows}
            </table>
        </DefaultLayout>
    )

}

export default Races;